
.v-slide-content {
	width: 100%;
	height: 120px;
	overflow: hidden;
}
.v-slide-box {
	height: 90px;
	bottom: 0px;
	box-sizing: border-box;
}
.v-slide-box li:first-child {
	margin-left: 15px;
}
.v-slide-box li {
	float: left;
	margin-right: 10px;
	height: 100%;
}
.v-slide-box img {
	width: auto;
	height: 100%;
	transition: all .2s ease-out;
	-webkit-transition: all .2s ease-out;
}
.slide-action {
	transition: transform .7s ease-out;
	-webkit-transition: transform .7s ease-out;
}
.click-select {
	height: 100px!important;
	margin-top: -10px;
	border: 2px solid #fff;
}



