
.play-date {
	background-color: #fff;
	padding-bottom: 10px;
}
.line {
	text-align: center;
	margin-top: 10px;
}
.lef {
	color: #999;
	font-weight: 400;
	font-size: 10px;
	margin-right: 1.5%;
}
.le {
	display: inline-block;
	width: 35%;
	border-bottom: 1px dashed #d7d7d7;
}
.ic {
	display: inline-block;
	width: 16px;
	height: 16px;
	border-radius: 100%;
	background-size: 90%;
	background-position: center center;
	background-repeat: no-repeat;
	margin-left: 1.5%;
}
.md {
	background-image: url('../../assets/images/md.svg');
	background-color: #b8dcf6;
}
.nd {
	background-image: url('../../assets/images/nd.svg');
	background-color: #faccaa;
}
.ad {
	background-image: url('../../assets/images/ad.svg');
	background-color: #cac1f8;
}
.pd, 
.pr-l {
	display: inline-block;
	width: 45%;
}
.pd div:nth-of-type(2) {
	margin-top: 2px;
}
.pdate li {
	height: 65px;
	padding: 15px 10px 10px 15px;
	border-bottom: 1px solid #ebebeb;
}
.pdate li:last-child {
	border-bottom: 0px;
}
.pdate em {
	font-size: 18px;
}
.pdate .buy-btn {
	left: 59%;
	bottom: 21px;
}
.fo {
	font-size: 12px;
	color: #999;
}
.f1 {
	margin-left: 22px;
}
.f3 {
	margin-left: 10px;
}
.f4 {
	display: block;
	text-decoration: line-through;
	margin: 2px 0;
}
.pr-l {
	text-align: right;
}
.prf {
	color: #fd2c32;
}



