
.mint-indicator-wrapper {
	z-index: 1000
}
#select-city {
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 9999999;
	background-color: #fff;
}
.city-header {
	height: 46px;
	line-height: 46px;
	text-align: center;
	color: #000;
	font-size: 16px;
	background-color: #fff;
}
.close-city {
	font-size: 28px;
	color: #666;
	display: inline-block;
	height: 46px;
	width: 50px;
	line-height: 38px;
	text-align: center;
	right: 0px;
}
@keyframes fadeInDown {
0% {
    opacity: .7;
    transform: translateY(-50px)
}
100% {
    opacity: 1;
    transform: translateY(0)
}
}
.fadeInDown {
  animation: fadeInDown .3s;
}
@keyframes fadeOutTop {
0% {
    opacity: 1;
    transform: translateY(0)
}
100% {
	  opacity: 0;
	  transform: translateY(-50px)
}
}
.fadeOutTop {
  animation: fadeOutTop .35s;
}



