
.cm-header {
	background: #fafafa;
    color: #333;
    height: 40px;
    line-height: 26px;
    font-size: 14px;
    left: 0px;
    right: 0px;
    top: 0px;
    display: -webkit-box;
    box-sizing: border-box;
    padding: 8px 0;
    border-bottom: 1px solid #ebebeb;
}
.cm-header>div {
	-moz-box-flex: 1.0;
	-webkit-box-flex: 1.0;
	text-align: center;
	box-sizing: border-box;
}
.area-item {
	background-color: #fff;
	font-size: 14px;
	color: #222;
	box-sizing: border-box;
	max-height: 70%;
	padding-left: 15px;
	overflow-y: scroll;
}
.area-item li {
	padding: 14px 0;
	border-bottom: 1px solid #ebebeb;
}



